import React from 'react';
import { connect } from "@cerebral/react";
import { state, signal } from "cerebral/tags";
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import {withStyles} from "material-ui/styles";

const styles = theme => ({
  textField: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "flex-start",
    marginRight: theme.spacing.unit,
    minWidth: "200px",
    width: "20%",
    color: "#FAFAFA",
    rightPadding: 10
  }
});

class MenuList extends React.Component {

  render() {

    return (

        <Menu
          id="simple-menu"
          open={this.props.open}
          onClose={this.handleClose}
        >
          <MenuItem onClick={()=>this.props.addOperatorClicked({})}>Add Operator</MenuItem>
          <MenuItem onClick={()=>this.props.clearConnection({})}>Reset Cache</MenuItem>
          <MenuItem onClick={()=>this.props.clearConnection({})}>Logout</MenuItem>
        </Menu>

    );
  }
}

export default connect(
  {
    menuItems:                  state`MenuList.records`,
    current:                    state`MenuList.current`,
    open:                       state`MenuList.open`,

    setCurrentItem:             signal`MenuList.setCurrentItem`,
    addOperatorClicked:         signal`operators.addOperatorClicked`,
    clearConnection:            signal`Connections.clearConnection`
  },
  withStyles(styles, { withTheme: true })(MenuList)
);
